.about-us {
  display: flex;
  flex-direction: column;
}

.mission-img {
  width: 100%;
  height: 40vh;
  background: #213658;
}

.mission-heading {
  text-align: center;
  font-size: 28px;
  padding: 15px;
  margin: 20px;
}

.mission-text {
  width: 60%;
  margin: 10px auto;
  padding: 8px;
  font-size: 20px;
  text-align: justify;
}

.team-img {
  width: 100%;
  height: 50%;
  margin: 20px auto;
}

.team-text {
  width: 60%;
  margin: 20px auto;
  margin-bottom: 70px;
  padding: 20px;
  padding-bottom: 40px;
  font-size: 20px;
  text-align: justify;
}

.team-text > h2 {
  text-align: center;
  font-size: 30px;
  padding-bottom: 20px;
}
